<template>
  <ec-container title="原烟包装规格管理" icon="onetomany" color-code="001">
    <ec-container-body>
      <ec-panel>
        <ec-panel-toolbar>
          <ec-panel-item>
            <ec-query-panel defaultFold @query="getTableData" @reset="onReset">
              <ec-query-item label="申请单位">
                <org-picker
                  allow-clear
                  limitLevel="2"
                  :disabled="orgLevelEk !== '1'"
                  v-model="listQuery.applyOrgCd"
                />
              </ec-query-item>
              <ec-query-item label="烟叶类型">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="listQuery.leafTypeCd"
                >
                  <a-select-option
                    v-for="type in leafTypeList"
                    :key="type.leafTypeCode"
                    :value="type.leafTypeCode"
                  >
                    {{ type.leafTypeName }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="包装类型">
                <ec-select showSearch allowClear placeholder="请选择" v-model="listQuery.mouldType">
                  <a-select-option
                    v-for="type in packagesTypes"
                    :key="type.value"
                    :value="type.label"
                  >
                    {{ type.label }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <!-- <template #inner>
                <ec-query-item label="状态">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="listQuery.specState"
                  >
                    <a-select-option
                      v-for="item in statusList"
                      :key="item.value"
                      :value="item.value"
                      >{{ item.label }}</a-select-option
                    >
                  </ec-select>
                </ec-query-item>
              </template> -->
            </ec-query-panel>
          </ec-panel-item>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout>
            <ec-v-item>
              <ec-panel-toolbar>
                <ec-panel-item-left>
                  <a-button type="primary" :loading="appearLoading" @click="onBack">退回</a-button>
                </ec-panel-item-left>
              </ec-panel-toolbar>
            </ec-v-item>
            <ec-v-item>
              <ec-table
                ref="table"
                @checkbox-all="onSelTableData"
                @checkbox-change="onSelTableData"
                @page-action="handlePagination"
                :pagination="listQuery"
                :data="tableData"
                :loading="loading"
              >
                <ec-table-col width="80" type="checkbox" align="center"></ec-table-col>
                <ec-table-col title="序号" width="80" type="seq" align="center"></ec-table-col>
                <ec-table-col title="申请单位" field="applyOrgName" align="center"></ec-table-col>
                <ec-table-col title="烟叶形态" field="leafTypeName" align="center"></ec-table-col>
                <ec-table-col title="包装类型" field="packType" align="center"></ec-table-col>
                <ec-table-col title="规格(KG/件)" field="spec" align="center"></ec-table-col>
                <ec-table-col title="状态" field="specState" align="center">
                  <template #default="{ row }">
                    <div v-if="row.specState === '0'" style="color: rgba(0, 0, 0, 0.447)">
                      <span style="margin-right: 4px">●</span>
                      <span>未上报</span>
                    </div>
                    <div v-else-if="row.specState === '1'">
                      <span style="color: #409eff; margin-right: 4px">●</span>
                      <span>已上报</span>
                    </div>
                    <div v-else style="color: rgba(0, 0, 0, 0.447)">
                      <span style="margin-right: 4px">●</span>
                      <span>已退回</span>
                    </div>
                  </template>
                </ec-table-col>
                <!-- <ec-table-col title="操作" field="specState" align="center">
                  <template #default="{ row }">
                    <a-button type="link" @click="onDetails(row)">详细</a-button>
                  </template>
                </ec-table-col> -->
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
      <ec-drawer
        title="详细"
        :is-confirm="true"
        size="small"
        :visible="visible"
        @close="visible = false"
      >
        <ec-container height="auto" fixed="top bottom" color-code="007">
          <ec-container-body>
            <ec-panel class="prodDrawer" color-code="001" title="基本信息">
              <ec-panel-item>
                <a-row type="flex" :gutter="12">
                  <a-col :span="12">
                    申请单位: <span>{{ formData.applyOrgName }}</span>
                  </a-col>
                  <a-col :span="12">
                    烟叶形态: <span>{{ formData.leafFormName }}</span>
                  </a-col>
                </a-row>
                <a-row type="flex" :gutter="12">
                  <a-col :span="12">
                    包装类型: <span>{{ formData.packType }}</span>
                  </a-col>
                  <a-col :span="12">
                    规格: <span>{{ !!formData.spec ? `${formData.spec}KG/件` : '' }}</span>
                  </a-col>
                </a-row>
                <a-row>
                  <a-col :span="12">
                    状态: <span>{{ formData.specState === '0' ? '未上报' : '已上报' }}</span>
                  </a-col>
                </a-row>
              </ec-panel-item>
            </ec-panel>
          </ec-container-body>
          <ec-container-footer fixed>
            <a-space>
              <a-button @click="visible = false">关闭</a-button>
            </a-space>
          </ec-container-footer>
        </ec-container>
      </ec-drawer>
    </ec-container-body>
  </ec-container>
</template>
<script>
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'MaterialApply',
  components: {
    OrgPicker,
  },
  data() {
    return {
      appearLoading: false,
      packagesTypes: [
        { label: '麻片', value: '1' },
        { label: '烟框', value: '2' },
        { label: '烟箱', value: '3' },
      ],
      statusList: [
        { label: '已上报', value: '1' },
        { label: '已退回', value: '2' },
        { label: '未上报', value: '0' },
      ],
      saveLoading: false,
      deleteLoading: false,
      usingLoading: false,
      mouldTypeList: [
        { name: '模板类型1', id: '1' },
        { name: '模板类型2', id: '2' },
      ],
      curType: 'add',
      formData: {},
      formRules: {
        applyOrgCd: [{ required: true, message: '请选择申请单位', trigger: 'change' }],
        leafTypeCd: [{ required: true, message: '请选择烟叶类型', trigger: 'change' }],
        spec: [{ required: true, message: '请输入规格', trigger: 'blur' }],
        packType: [{ required: true, message: '请选择包装类型', trigger: 'change' }],
      },
      visible: false,
      tableData: [],
      loading: false,
      listQuery: {
        applyOrgCd: undefined,
        mouldName: undefined,
        mouldType: undefined,
        pageSize: 10,
        pageNo: 1,
        total: 0,
      },
      total: 0,
      selectData: [],
      leafTypeList: [],
    };
  },
  computed: {
    _api() {
      return this.$apis.packingSpec.apply;
    },
    bizCode() {
      return this.$store.getters.getAppConfig.bizCode;
    },
    orgLevelEk() {
      return this.$store.getters.getAppConfig.orgLevelEk;
    },
  },
  methods: {
    init() {
      // 默认状态
      if (this.orgLevelEk !== '1') {
        this.listQuery.applyOrgCd = this.bizCode;
      }
    },
    onDetails(row) {
      this.formData = { ...row };
      this.visible = true;
    },
    orgChange(data) {
      const { title } = data.selectOption;
      this.formData.applyOrgName = title;
    },
    onReset() {
      const { total } = this.listQuery;
      this.listQuery = {
        mouldName: undefined,
        mouldType: undefined,
        pageSize: 10,
        pageNo: 1,
        applyOrgCd: undefined,
        total,
      };
    },
    onBack() {
      if (!this.selectData.length) {
        return this.$message.error('请先选择数据');
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认退回选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.appearLoading = true;
          const ids = [];
          for (const item of this.selectData) {
            const { packSpecId } = item;
            ids.push(packSpecId);
          }
          const params = {
            packSpecId: ids,
          };
          this._api
            .retreatPcTPackSpec(params)
            .then(() => {
              this.getTableData();
              this.$message.success('退回成功');
            })
            .finally(() => {
              this.selectData = [];
              this.appearLoading = false;
            });
        },
      });
    },

    onSelTableData({ records }) {
      this.selectData = records;
    },

    handlePagination(pageNO, pageSize) {
      console.log(this.listQuery);
      this.listQuery.pageNo = pageNO;
      this.listQuery.pageSize = pageSize;
      this.getTableData();
      // this.onQuery();
    },
    getTableData() {
      this.loading = true;
      const { ...params } = this.listQuery;

      if (typeof params.applyOrgCd !== 'string' && params.applyOrgCd !== undefined) {
        params.applyOrgCd = params.applyOrgCd.join('');
      }
      this._api
        .pageQuerySourceManagePcTPackSpec(params)
        .then((data) => {
          this.tableData = data?.rows;
          console.log(this.tableData);
          this.listQuery.total = data?.totalRows;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getLeafTypes() {
      $ec.apis.bim.leafType.query({}).then(({ rows }) => {
        // this.leafTypeList = data?.rows;
        this.leafTypeList = rows?.filter((item) => item.lastLevelFlag === '0') || [];
      });
    },
  },
  created() {
    this.init();
    this.getLeafTypes();
  },
};
</script>
<style lang="less">
.moduDrawer {
  .ant-col {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    span {
      padding-left: 5px;
    }
  }
}
.moduDrawer .mouldContent {
  padding: 16px 45px 16px 16px;
  background-color: #f2f4f5;
  border-radius: 6px;
  min-height: 300px;
  overflow-y: auto;
  margin: 5px 0 8px 0;
  color: #393b3d;
  font-weight: 700;
}
</style>
